//灰階文字
.text-gray-500{
    color: $gray-500;
}

//input相關
.form-control{
    font-size: map_get($font-sizes, 0);
    font-weight:  $font-weight-normal;
    // padding: $input-padding-y $input-padding-x;
}
.form-label{
    font-weight: $font-weight-normal;
}

.form-check-label {
    font-weight: $font-weight-normal;
    font-size: map-get($font-sizes, 0);
}
.form-listarea-box{
    width: 17.5rem;
}
.form-check-mt{
    margin-top: 0.2rem;
}
.form-check-zindex{
    z-index: 9999;
    position: relative;
}


//按鈕區
.btn-export::after{
    display: none;
}

//通用區
.zindex-1{
    z-index: 1;
}
.zindex-2{
    z-index: 2;
}
.poa{
    position: absolute;
}
.por{
    position: relative;
}
.right-0{
    right: 0;
}
.mt--05{
    margin-top: -0.25rem;
}


//alert
.alert-bk{
    position: absolute;
    width: 80%;
    left: 10%;
    top: 10%;
}

//signIn登入頁
.signIn-captchaImg{
    // width: 120px;
    position: absolute;
    right: 0;
    border-radius: 0 $btn-border-radius $btn-border-radius 0;
    height: 42px;
    top: 0px;
    left: inherit;
    // line-height: 0;
}

//pattern區
//header
//Logo.pug
.logoCh{
    letter-spacing: 0em;
}
