//model區
//2層分類列表
.model-classify-toogleArea{
    padding-right: 80px;

}
.model-classify-input{
    width: 160px;
    display: none;
    @include media-breakpoint-up(sm) {
        width: 16.875rem;
    }
}

.model-classifyBt-trash{
   position: absolute;
   right: 0;
}
.model-classifyBt-edit{
    position: absolute;
    right: 40px;
 }
 .model-classifyBt-view{
    position: absolute;
    right: 80px;
 }
