.model-classify-label{
    position: absolute;
    z-index: 1;
}
.model-classify-input{
    position: absolute;
    z-index: 2;
}
.model-classify-input:focus{
    ~ .model-classify-label{
        display: none;
    }
}

//元件區
//spinner
#spinner-hide{
    display: none;
    z-index: 9999;
}

//icon
.icon-gray{
    color: $gray-400;
}
.icon-size18{
    width: 18px;
    height: 18px;
}
